<template>
	<view class="content">
		<view class="person-item userinfom">
			<view>
				<text>头像</text>
				<view class="user-right">
					<image :src="headUrl?base_url+'/img'+headUrl:'../../../static/home/500.jpg'" mode=""></image>
					<image src="../../../static/mine/tos.png" mode=""></image>
				</view>
			</view> 
			<view>
				<text>姓名</text>
				<view class="user-right">
					<text>{{userInfo.userName}}</text>
					<image src="../../../static/mine/tos.png" mode=""></image>
				</view>
			</view>
			<view @tap="toChangePwd">
				<text>修改密码</text>
				<view class="user-right">
					<image src="../../../static/mine/tos.png" mode=""></image>
				</view>
			</view>
		</view>
		<view class="person-item userinfom">
			<view>
				<text>紧急电话</text>
				<view class="user-right">
					<text>{{userInfo.urgencyPhone}}</text>
					<image src="../../../static/mine/tos.png" mode=""></image>
				</view>
			</view>
			<view>
				<text>正常电话</text>
				<view class="user-right">
					<text>{{userInfo.phone}}</text>
					<image src="../../../static/mine/tos.png" mode=""></image>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userInfo:{},
				headUrl:''
			}
		},
		onLoad() {
			this.getUserInfo()
		},
		methods: {
			toChangePwd(){
				uni.navigateTo({
					url:'./changePwd'
				})
			},
			// 获取用户信息
			getUserInfo(){
				let api = '/user/info';
				let data = { userId:uni.getStorageSync('userInfo').userId }
				this.http(api,'GET',data).then(res=>{
					console.log(res);
					if(res.code==200){
						this.userInfo = res.data
						this.headUrl = res.data.headUrl
					}
				})
			},
		}
	}
</script>

<style scoped>
	 .userinfom{
		 padding:  0 14rpx;
		 box-sizing: border-box;
	 }
	 .userinfom>view{
		 display: flex;
		 width: 100%;
		 padding: 25rpx 0 25rpx 13rpx;
		 box-sizing: border-box;
		 justify-content: space-between;
		 align-items: center;
		 border-bottom:1rpx solid #fff;
	 }
	 .userinfom>view:last-child{
		 border-bottom:0
	 }
	 .userinfom>view text{
		 font-size: 28rpx;  
	 }
	 .user-right{
		 display: flex;
		 align-items: center;
	 }
	 .user-right>image:nth-last-child(1){
		 width: 16rpx;
		 height: 28rpx;
	 }
	 .user-right>image:nth-last-child(2){
		 width: 80rpx;
		 height: 80rpx;
		 border-radius: 50%;
		 margin-right: 24rpx;
	 }
	 .user-right>text{
		 margin-right: 24rpx;
		 font-size: 24rpx;
	 }
</style>
